let validateMail = new JustValidate('.mailing', {
    errorLabelStyle: {
        color: '#f06666',
    }
});
let validateForm = new JustValidate('.form', {
    errorLabelStyle: {
        color: '#ff3030',
    }
});

validateMail
    .addField('.input', [
        {
        rule: 'required',
        errorMessage: 'Обязательное поле',
        },
        {
        rule: 'email',
        errorMessage: 'Недопустимый формат',
        },
    ]);

validateForm
    .addField('#name', [
        {
        rule: 'required',
        errorMessage: 'Обязательное поле',
        },
        {
        rule: 'minLength',
        value: 3,
        errorMessage: 'Имя слишком короткое',
        },
        {
        rule: 'maxLength',
        value: 30,
        errorMessage: 'Имя слишком длинное',
        },
        {
            rule: 'customRegexp',
            value: /(?=.*[a-z])(?=.*[A-Z])/ || /(?=.*[а-я])(?=.*[А-Я])/,
            errorMessage: 'Недопустимый формат',
        },
    ])
    .addField('#email', [
        {
        rule: 'required',
        errorMessage: 'Обязательное поле',
        },
        {
        rule: 'email',
        errorMessage: 'Недопустимый формат',
        },
    ])
