let searchBtn = document.querySelector('.search__btn');
let searchBlock = document.querySelector('.search__block');
let searchClose = document.querySelector('.search__close')

const toggleSearchBlock = () => {
  searchBlock.classList.toggle('search-active');
}

searchClose.addEventListener('click', e => {
    e.stopPropagation();

    toggleSearchBlock();
  });
searchBtn.addEventListener('click', e => {
  e.stopPropagation();

  toggleSearchBlock();
});

document.addEventListener('click', e => {
  let target = e.target;
  let its_searchBlock = target == searchBlock || searchBlock.contains(target);
  let its_searchBtn = target == searchBtn;
  let searchBlock_is_active = searchBlock.classList.contains('search-active');

  if (!its_searchBlock && !its_searchBtn && searchBlock_is_active) {
    toggleSearchBlock();
  }
})

